import { DATE_FORMAT_STRING } from "@/config";
import { emitter } from "@/event-bus";
import { format } from "date-fns";
import { computed, reactive, ref } from "vue";
import { useStore } from "vuex";
import useVuelidate from "@vuelidate/core";
import { required } from "@vuelidate/validators";
import { useToast } from "primevue/usetoast";

export function useCycleReportForm() {
    const store = useStore();
    const form = reactive({
        cycleTypes: [],
        funded: null,
        from: null,
        to: null,
        cancelled: null,
        siteId: null
    });

    const cycleTypesOpntions = computed(
        () => store.state.cycleReport.cycleTypes
    );
    const fundedOptions = ref([
        { label: "All", value: -1 },
        { label: "Yes", value: 1 },
        { label: "All", value: 0 }
    ]);
    const cancelOptions = ref([
        { label: "All", value: "all" },
        { label: "Yes", value: "true" },
        { label: "No", value: "false" }
    ]);
    const locationOptions = computed(
        () => store.state.cycleReport.locationOptions
    );

    const rules = {
        cycleTypes: { required },
        funded: { required },
        from: { required },
        to: { required },
        cancelled: { required },
        siteId: { required }
    };

    const v$ = useVuelidate(rules, form);
    const submitted = ref(false);

    const getCycleReport = () => {
        submitted.value = true;
        if (v$.value.$invalid) {
            return;
        }

        emitter.emit("cycle-report-table:get-cycle-report", {
            ...form,
            cycleTypes: Object.values(form.cycleTypes),
            to: format(form.to, DATE_FORMAT_STRING),
            from: format(form.from, DATE_FORMAT_STRING)
        });
    };

    const loading = ref(false);
    const getReportExcel = () => {
        submitted.value = true;
        if (v$.value.$invalid) {
            return;
        }
        loading.value = true;
        store
            .dispatch("cycleReport/getCycleReportExcel", {
                ...form,
                cycleTypes: Object.values(form.cycleTypes),
                to: format(form.to, DATE_FORMAT_STRING),
                from: format(form.from, DATE_FORMAT_STRING)
            })
            .then((data) => {
                window.open(data.link, "_blank");
            })
            .finally(() => (loading.value = false));
    };

    const getLocationOption = () => {
        store.dispatch("cycleReport/getLocationOption");
    };

    const getAllCycleTypes = () => {
        store.dispatch("cycleReport/getAllCycleTypes");
    };

    return {
        form,
        cycleTypesOpntions,
        fundedOptions,
        cancelOptions,
        locationOptions,
        getCycleReport,
        getAllCycleTypes,
        getLocationOption,
        submitted,
        v$,
        getReportExcel,
        loading
    };
}

export function useCycleReportTable() {
    const store = useStore();
    const toast = useToast();
    const loading = ref(false);
    const report = computed(() => store.state.cycleReport.report);
    const getCycleReport = (reqPayload) => {
        loading.value = true;
        store
            .dispatch("cycleReport/getCycleReport", reqPayload)
            .then(() => {})
            .catch(() => {
                toast.add({
                    severity: "warn",
                    summary: "",
                    detail: "No Data Found",
                    life: 3000
                });
            })
            .finally(() => {
                loading.value = false;
            });
    };

    return {
        report,
        loading,
        getCycleReport
    };
}
